@import url(//fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans';
  color: #343A40;
}

:root {
  --primary-color: #00a3f0;
  --secondary-color: #838383;
  --warning-color: #dca100;
  --danger-color: #cb1a1a;
  --success-color: #28a745;
}

.green-color{
  color: #2fce6c !important;
}

.yellow-color{
  color: #dca100 !important;
  color: var(--warning-color) !important;

}

.primary-color {
  color: #00a3f0 !important;
  color: var(--primary-color) !important;
}

.secondary-color {
  color: #838383 !important;
  color: var(--secondary-color) !important;
}

.secondary-color-darker {
  color: #5A5A5AFF;
}

.warning-color {
  color: #dca100 !important;
  color: var(--warning-color) !important;
}

.danger-color {
  color: #cb1a1a !important;
  color: var(--danger-color) !important;
}

.upload-buttons {
  padding: .3rem .6rem;
  border: solid 1px #fff;
  border-radius: 5px;
  color: #555;
  background-color: #f0f0f0;
  font-size: 12pt;
  font-weight: 500;
}

.pointer-icon{
cursor: pointer;
}

.custom-button {
  font-size: medium;
  border: 1px solid #45A2F8;
  color: white;
  padding: 0.25rem;
  font-family: 'Open Sans';
  font-style: normal;
  background: #23A8E0;
  border-radius: 4px;
}

.custom-button-disabled {
  font-size: medium;
  border: none;
  color: #C4C4C4;
  padding: 0.25rem;
  font-family: 'Open Sans';
  font-style: normal;
  background: #DEE2E6;
  border-radius: 4px;
  pointer-events: none;
}

.custom-button:hover {
  color: #45A2F8;
  background-color: #efefef;
}

.upload-buttons:hover:disabled {
  color: #00a3f0;
  color: var(--primary-color);
  border-color: #00a3f0;
  border-color: var(--primary-color);
}

.APPROVATO {

  flex-direction: column;

  color: white;
  font-style: normal;
  font-weight: 400;

  text-align: center;
  vertical-align: middle;

  width: 100px;
  height: 25px;

  background: #139518;
  border-radius: 40px;

}

.RIFIUTATO {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  font-style: normal;
  font-weight: 400;

  width: 100px;
  height: 25px;

  background: #cb1a1a !important;

  background: var(--danger-color) !important;
  border-radius: 40px;

}

.IN_LAVORAZIONE {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  font-style: normal;
  font-weight: 400;

  width: 150px;
  height: 25px;

  background: #dca100 !important;

  background: var(--warning-color) !important;
  border-radius: 40px;
}

.APPROVATO-select{
  border-radius: 5px;
  border-color: #28a745 !important;
  border-color: var(--success-color) !important;
  color: #6C757D;
  font-weight: 600;
  border: 2px solid;
  padding: 0.2rem;
  height: 2rem;

}


.RIFIUTATO-select{
  border-radius: 5px;
  border-color: #cb1a1a !important;
  border-color: var(--danger-color) !important;
  color: #6C757D;
  font-weight: 600;
  border: 2px solid;
  padding: 0.2rem;
  height: 2rem;

}


.IN_LAVORAZIONE-select{
  border-radius: 5px;
  border-color: #dca100 !important;
  border-color: var(--warning-color) !important;
  color: #6C757D;
  font-weight: 600;
  border: 2px solid;
  padding: 0.2rem;
  outline: 0 !important;
  height: 2rem;

}

*,*:focus,*:hover{
  outline:none;
}

.doc {
  border-style: hidden;
  padding: 0.5rem;

}

.border-hidden {
  border-style: hidden;
}

.doc:hover, .doc:focus {
  border: 2px solid  #00a3f0 !important;
  border: 2px solid  var(--primary-color) !important;
  border-radius: 5px;
  padding: 0.5rem;

}

.white-rounded-shadowed {
  background-color: #fff;
  box-shadow: #d1d1d1 0 4px 3px 1px;
  padding: 3rem;
  margin: 1rem;

}

.pipe{

border-right: 2px solid #D4D9DE ;
  text-align: center;
  vertical-align: middle;
}

hr {
  margin-top: 1rem;
  border-top: 1px solid #D4D9DE;
}

.desc-text {

  font-size: 15px;
  font-weight: 500;
  color: #7F8297;
}

.document-text {
  text-transform: capitalize;
  color: #404253;
  font-weight: 500;
}
.required:after {
  content:" *";
  color: red;
}
.pratica-title {
  height: 24px;
  left: 35px;
  top: 81px;

  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height, or 120% */

  color: #4C4E5D;


}

.align-to-end {
  text-align: end;
}

.align-to-center {
  text-align: center;
}

.default-color {
  color: #343A40;
}

.highlighted-span {
  color: #00a3f0;
  background-color: #eee;
  font-weight: 600;
  padding: .3rem;
  margin-left: .5rem;
  border-radius: 5px;
}

.highlighted-span-light {
  color: #00a3f0;
  background: #F3FAFE;
  font-weight: 600;
  padding: .3rem;
  margin-left: .5rem;
  border-radius: 5px;
}



.back-col {
  background-color: #E5E5E5;
  padding-bottom: 5rem;
  padding-top: 1rem;
  padding-left: 5rem;
  padding-right: 5rem;
}

.margin-custom-b {
  margin-bottom: 3rem;

}

.margin-custom-l {
  margin-left: 1rem;

}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

